    const getApplicationDetail = () => {


        let token = window.localStorage.getItem('citizen_token')
        // console.log('token at basic details is  get method...', token)
        const header = {
            headers:
            {
                Authorization: `Bearer ${token}`,
                Accept: 'application/json',
            }
        }
        axios.post(`${api_getAppicationFullDetail}`,
            {
                id: safId
            },
            header)
            .then(function (response) {
                console.log('view full details...', response.data)
                setapplicationFullData(response?.data?.data?.fullDetailsData)
            })
            .catch(function (error) {
                console.log('==2 details by id error...', error)
            })
    }

    useEffect(() => {
        getApplicationDetail()
    }, [])


